import React from "react"
import { MyLib } from "@macashipo/mlib"
const BaseSelect2 = props => {
  const options =
    props.options ||
    (props.fnList &&
      props.fnList.getSourceData &&
      props.fnList.getSourceData()) ||
    []
  // console.warn("render select2:", props.value)
  return (
    <MyLib
      type="select2"
      value={props.value}
      // value={"1"}
      onChange={newValue => {
        console.warn("change:", newValue)
        props.onChange && props.onChange(newValue)
      }}
      className={props.className}
      style={props.style}
      simpleValue //ko con su dung duoc nua
      options={options}
      {...props.attrs}
    />
  )
}

export default BaseSelect2
